<template lang="html">
    <div class="ex-link-block-wrapper">
        <a class="ex-link-block" :href="href">
            <div class="ex-link-details">
                <p class="ex-link-text">{{ title }}</p>
                <p class="ex-link-website">{{ this.website }}</p>
            </div>
            <div class="arrow-wrapper">
                <stIconArrowRightDown/>
            </div>
        </a>
    </div>
</template>
  
<script>
import stIconArrowRightDown from '@/components/common/icons/arrow_right_down.vue'
export default {
    name: "stExteranlLinkBlock",
    components: {
      stIconArrowRightDown,
    },
    props: {
        title: String,
        href: String
    },
    data() {
        return {
            website: this.href.match(/^https?:\/\/([\w.]*).*$/)[1]
        };
    }
}
</script>

